<template><section>

<h1>Layout 布局</h1>
<p>协助进行页面级整体布局。</p>
<h2 id="she-ji-gui-ze"><a class="header-anchor" href="#she-ji-gui-ze" aria-hidden="true">¶</a> 设计规则</h2>
<h3 id="chi-cun"><a class="header-anchor" href="#chi-cun" aria-hidden="true">¶</a> 尺寸</h3>
<p>一级导航项偏左靠近 logo 放置，辅助菜单偏右放置。</p>
<ul>
<li>顶部导航（大部分系统）：一级导航高度 <code v-pre="">64px</code>，二级导航 <code v-pre="">48px</code>。</li>
<li>顶部导航（展示类页面）：一级导航高度 <code v-pre="">80px</code>，二级导航 <code v-pre="">56px</code>。</li>
<li>顶部导航高度的范围计算公式为：<code v-pre="">48+8n</code>。</li>
<li>侧边导航宽度的范围计算公式：<code v-pre="">200+8n</code>。</li>
</ul>
<h3 id="jiao-hu"><a class="header-anchor" href="#jiao-hu" aria-hidden="true">¶</a> 交互</h3>
<ul>
<li>一级导航和末级的导航需要在可视化的层面被强调出来；</li>
<li>当前项应该在呈现上优先级最高；</li>
<li>当导航收起的时候，当前项的样式自动赋予给它的上一个层级；</li>
<li>左侧导航栏的收放交互同时支持手风琴和全展开的样式，根据业务的要求进行适当的选择。</li>
</ul>
<h3 id="shi-jue"><a class="header-anchor" href="#shi-jue" aria-hidden="true">¶</a> 视觉</h3>
<p>导航样式上需要根据信息层级合理的选择样式：</p>
<ul>
<li>
<p><strong>大色块强调</strong></p>
<p>建议用于底色为深色系时，当前页面父级的导航项。</p>
</li>
<li>
<p><strong>高亮火柴棍</strong></p>
<p>当导航栏底色为浅色系时使用，可用于当前页面对应导航项，建议尽量在导航路径的最终项使用。</p>
</li>
<li>
<p><strong>字体高亮变色</strong></p>
<p>从可视化层面，字体高亮的视觉强化力度低于大色块，通常在当前项的上一级使用。</p>
</li>
<li>
<p><strong>字体放大</strong></p>
<p><code v-pre="">12px</code>、<code v-pre="">14px</code> 是导航的标准字号，14 号字体用在一、二级导航中。字号可以考虑导航项的等级做相应选择。</p>
</li>
</ul>
<h2 id="zu-jian-gai-shu"><a class="header-anchor" href="#zu-jian-gai-shu" aria-hidden="true">¶</a> 组件概述</h2>
<ul>
<li><code v-pre="">Layout</code>：布局容器，其下可嵌套 <code v-pre="">Header</code> <code v-pre="">Sider</code> <code v-pre="">Content</code> <code v-pre="">Footer</code> 或 <code v-pre="">Layout</code> 本身，可以放在任何父容器中。</li>
<li><code v-pre="">Header</code>：顶部布局，自带默认样式，其下可嵌套任何元素，只能放在 <code v-pre="">Layout</code> 中。</li>
<li><code v-pre="">Sider</code>：侧边栏，自带默认样式及基本功能，其下可嵌套任何元素，只能放在 <code v-pre="">Layout</code> 中。</li>
<li><code v-pre="">Content</code>：内容部分，自带默认样式，其下可嵌套任何元素，只能放在 <code v-pre="">Layout</code> 中。</li>
<li><code v-pre="">Footer</code>：底部布局，自带默认样式，其下可嵌套任何元素，只能放在 <code v-pre="">Layout</code> 中。</li>
</ul>
<blockquote>
<p>注意：采用 flex 布局实现，请注意<a href="http://caniuse.com/#search=flex">浏览器兼容性</a>问题。</p>
</blockquote>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
