                  <div slot="description"><h4 id="xiang-ying-shi-bu-ju"><a class="header-anchor" href="#xiang-ying-shi-bu-ju" aria-hidden="true">¶</a> 响应式布局</h4>
<p>Layout.Sider 支持响应式布局。</p>
<blockquote>
<p>说明：配置 <code>breakpoint</code> 属性即生效，视窗宽度小于 <code>breakpoint</code> 时 Sider 缩小为 <code>collapsedWidth</code> 宽度，若将 <code>collapsedWidth</code> 设置为零，会出现特殊 trigger。</p>
</blockquote>
