<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="sider-props"><a class="header-anchor" href="#sider-props" aria-hidden="true">¶</a> Sider Props</h3>
<table class="table"><thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>collapsed</td>
<td>当前收起状态，可用v-model</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
</tr>
<tr>
<td>collapsible</td>
<td>是否可收起</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
</tr>
<tr>
<td>trigger</td>
<td>是否显示trigger，collapsible 为 true 时有效，设置为 false 时隐藏 trigger</td>
<td>Boolean</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>宽度</td>
<td>Number</td>
<td><code v-pre="">200</code></td>
</tr>
<tr>
<td>collapsedWidth</td>
<td>收缩宽度，仅当 <code v-pre="">collapsed: true</code> 时生效</td>
<td>Number</td>
<td><code v-pre="">64</code></td>
</tr>
<tr>
<td>breakpoint</td>
<td>触发响应式布局的断点</td>
<td>Enum { 'xs', 'sm', 'md', 'lg', 'xl' }</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="sider-events"><a class="header-anchor" href="#sider-events" aria-hidden="true">¶</a> Sider Events</h3>
<table class="table"><thead>
